 const [totalTransactions,setTotalTransactions] = useState("Loading..");
 const [totalProfitMade,setTotalProfitMade] = useState("Loading..");


  useEffect(() => {
    
getTotalNumberOfEvents();
getTotalNumberOfUsers();
getTotalNumberOfFans();
getTotalNumberOfCelebrities();
getTotalNumberOfFailedTransactions();
getTotalNumberOfTransactions();
getTotalAmountOfProfitMade();

    return () => {
   
    }
  }, []);

